import React from "react";
import { Link } from "react-router-dom";

export default function Home({ history }) {
  if (navigator.serviceWorker) {
    navigator.serviceWorker.onmessage = (event) => {
      console.log(event);
      // no global state for now
      window._file = event.data.file;
      history.push("/send");
    };
  }

  return (
    <div>
      <Link to="/send">Enviar arquivo</Link>
      <Link to="/receive">Receber um arquivo</Link>
      <footer>
        Instala na home!
      </footer>
    </div>
  );
}
