import React from 'react'
import QrScanner from 'qr-scanner'; 
import { useRef } from 'react';
import { useEffect } from 'react';
QrScanner.WORKER_PATH = 'lib/qr-scanner-worker.min.js';


export default function Send() {
    const file = window._file || {name: 'nenhum arquivo'};
    const videoRef = useRef(null);

    useEffect(() => {
        const qrScanner = new QrScanner(videoRef.current, (result) => alert(result));
        qrScanner.start();
        return () => {
            qrScanner.stop();
        }
    }, []);

    return (
        <div>
            send page.
            <header>
            pronto pra enviar {file.name}
            </header>
            <div>acessa {process.env.PUBLIC_URL} no outro dispositivo</div>

            <video ref={videoRef} className="camera"></video>

            <div className="typecode">
                <label htmlFor="code">ou digite o código</label>
                <input type="text" id="code"/>
            </div>

        </div>
    )
}
